.profession {
    background-image: url(../IMG/hero2.png);
    background-size: cover;
    height: 500px;
    width: 100%;
}

.profession-img {
    margin-left: auto;
    margin-right: auto;
    margin-top: 70px;
}

.profession-div {
    display: flex;
}

.profession-text {
    max-width: 780px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    color: #e9e8eb;
    font-family: Playfair Display;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.3;
}

.profession-name {
    margin-top: 50px;
    text-align: center;
    font-family: Roboto;
    font-weight: 900;
    font-size: 12px;
    line-height: 1.6;
    color: #f9f9f9;
}

.profession-work {
    margin-top: 13px;
    text-align: center;
    font-family: Roboto;
    font-weight: 300;
    font-size: 11px;
    line-height: 1.7;
    color: #f9f9f9;
}

.container-profession {
    width: 312px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
}

.splide__arrow {
    border: 0;
    cursor: pointer;
    padding: 0;
    position: absolute;
    top: 28%;
    transform: translateY(-50%);
    z-index: 2;
    opacity: 0.7;
    align-items: center;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    height: 2em;
    max-width: 2em;
}

.splide__arrow--next {
    right: 1em;
    margin-right: -50px;
}

.splide__arrow--prev {
    left: 1em;
    margin-left: -50px;
}